<div class="main-content"><div class="main-content-left"><p>{{ Math.round(getFullWeather.current.temp) }}°</p><p>Сегодня</p><p>{{descr}}</p><p>Город: {{ getFullWeather.name }}</p><img src="../assets/img/sun.svg" v-if="descr == 'ясно'"><img src="../assets/img/small_rain.svg" v-else-if="descr == 'небольшой дождь'"><img src="../assets/img/small_rain.svg" v-else-if="descr == 'легкий дождь'"><img src="../assets/img/oblochnost.png" v-else-if="descr == 'небольшая облачность'"><img src="../assets/img/oblochnost.png" v-else-if="descr == 'облачно с прояснениями'"><img src="../assets/img/oblochnost.png" v-else-if="descr == 'переменная облачность'"><img src="../assets/img/rain.svg" v-else-if="descr == 'дождь'"><img src="../assets/img/mainly_cloudy.svg" v-else-if="descr == 'пасмурно'"><img src="../assets/img/fog.webp" v-else-if="descr == 'туман'"><img src="../assets/img/снег.png" v-else-if="descr == 'небольшой снегопад'"><img src="../assets/img/снег.png" v-else-if="descr == 'небольшой снег'"><img src="../assets/img/снег.png" v-else-if="descr == 'снег'"><img src="../assets/img/dimka.png" v-else-if="descr == 'дымка'"><img src="../assets/img/sneganddojd.png" v-else-if="descr == 'снег с дождём'"><img src="../assets/img/sneganddojd.png" v-else-if="descr == 'дождь с снегом'"><img src="../assets/img/else.jpg" v-else></div><div class="main-content-right"><div> <div class="border"><img src="../assets/img/temp.svg" alt=""></div><span>Температура</span><p>{{ Math.round(getFullWeather.current.temp) }}° - ощущается как {{ Math.round(getFullWeather.current.feels_like) }}°</p></div><div> <div class="border"><img src="../assets/img/pressure.svg" alt=""></div><span>Давление</span><p>{{getFullWeather.current.pressure}} мм ртутного столба</p></div><div> <div class="border"><img src="../assets/img/precipitation.svg" alt=""></div><span>Влажность</span><p>{{getFullWeather.current.humidity}} %</p></div><div> <div class="border"><img src="../assets/img/wind.svg" alt=""></div><span>Ветер</span><p>{{getFullWeather.current.wind_speed}} м/с</p></div><img src="../assets/img/cloud.svg" alt=""></div></div>