<div class="main-week-day"><h2>{{dayTime}} {{ monthTime }}</h2><img src="../assets/img/sun.svg" v-if="descr == 'ясно'"><img src="../assets/img/small_rain.svg" v-else-if="descr == 'небольшой дождь'"><img src="../assets/img/small_rain.svg" v-else-if="descr == 'легкий дождь'"><img src="../assets/img/oblochnost.png" v-else-if="descr == 'небольшая облачность'"><img src="../assets/img/oblochnost.png" v-else-if="descr == 'облачно с прояснениями'"><img src="../assets/img/oblochnost.png" v-else-if="descr == 'переменная облачность'"><img src="../assets/img/rain.svg" v-else-if="descr == 'дождь'"><img src="../assets/img/mainly_cloudy.svg" v-else-if="descr == 'пасмурно'"><img src="../assets/img/fog.webp" v-else-if="descr == 'туман'"><img src="../assets/img/снег.png" v-else-if="descr == 'небольшой снегопад'"><img src="../assets/img/снег.png" v-else-if="descr == 'небольшой снег'"><img src="../assets/img/снег.png" v-else-if="descr == 'снег'"><img src="../assets/img/dimka.png" v-else-if="descr == 'дымка'"><img src="../assets/img/sneganddojd.png" v-else-if="descr == 'снег с дождём'"><img src="../assets/img/sneganddojd.png" v-else-if="descr == 'дождь с снегом'"><img src="../assets/img/else.jpg" v-else><span>{{ Math.round(day.temp.day) }}°</span><p>{{ Math.round(day.feels_like.day) }}°</p><p>{{ day.weather[0].description }}</p></div>